<template>
  <div class="dashboard">
    <div class="row w-100 p-0 m-0">
      <div class="col-lg-6 offset-lg-3 col-md-8 offset-md-2 col-sm-12 p-2 m-0">
        <div class="vehicle-view">
          <img
            class="img-fluid rounded rounded-lg border"
            src="../assets/va-poster.jpg"
            alt="vishwakarma-automobiles-poster"
            draggable="false"
          />
        </div>

        <div v-if="loading" class="d-flex justify-content-center my-3">
          <div class="spinner-border" role="status">
            <span class="visually-hidden">Loading...</span>
          </div>
        </div>

        <!-- Display Vehicle Number Plate -->
        <div
          v-if="vehicle"
          class="number-plate bg-white text-dark text-center p-3 my-2"
        >
          <h1 v-if="vehicle" class="fw-bold p-0 m-0">
            {{ vehicle.vehicleNo.toUpperCase() }}
            <br />
          </h1>
          <p v-if="vehicle" class="m-0 p-0">{{ vehicle.vehicleModel }}</p>
        </div>

        <!-- Displaying Not found on invalid Vehicle No -->
        <h1
          v-if="!vehicle && !loading"
          class="number-plate bg-white text-danger text-center p-3 mt-3 fw-bold border-danger"
        >
          <div>
            <s> {{ $route.params.VehicleId }}</s>
          </div>
        </h1>

        <!-- Alert Message -->
        <p v-if="!vehicle" class="alert alert-warning text-center alert-sm p-1">
          No Vehicle{s} found :
          <span class="fw-bold">{{ $route.params.VehicleId }}</span>
        </p>

        <!-- Add Service Button -->
        <div class="d-grid">
          <button @click="newService()" class="btn btn-primary btn-lg">
            New Service +
          </button>
        </div>
        <!-- SERVICES -->
        <keep-alive>
          <div v-if="services" class="services my-3 list-group mb-5">
            <h4 class="text-center">Services</h4>
            <div
              v-for="service in services"
              :key="service.serviceId"
              class="service border p-2 mb-2 list-group list-group-item"
            >
              <div class="d-flex justify-content-between">
                <p class="service-id fw-bold m-0">
                  {{ service.serviceId }}
                  <span v-show="service.serviceType == 'PS'"> - P/S</span>
                  <span v-show="service.serviceType == 'RR'"> - R/R</span>
                  <span class="ms-1 fw-normal">
                    - {{ service.vehicleKm }} Km
                  </span>
                </p>
                <p class="service-date m-0">
                  <span v-if="service.serviceDate">
                    {{
                      service.serviceDate
                        .slice(0, 10)
                        .split("-")
                        .reverse()
                        .join("-")
                    }}
                  </span>
                </p>
              </div>
              <p class="d-flex justify-content-between mb-1"></p>
              <p class="d-flex justify-content-between mb-1">
                <span>{{ service.customerName }}</span>
              </p>
              <div class="service-bill">
                <p class="d-flex justify-content-between">
                  <span>Total Amount </span>
                  <span class="fw-bold">Rs.{{ service.serviceTotal }}</span>
                </p>
                <div class="service-bill-preview text-center">
                  <div class="d-flex justify-content-between">
                    <!-- Open a bill here -->
                    <router-link
                      tag="a"
                      :to="'/bills/' + service.bill.billNumber"
                      v-if="service.bill"
                      class="btn btn-sm btn-primary px-3 text-decoration-none me-1"
                      >Bill {{ service.bill.billNumber }}
                    </router-link>
                    <a
                      class="btn btn-outline-secondary px-3 btn-sm me-auto"
                      :href="'tel:' + service.phoneNumber"
                    >
                      <img
                        src="../assets/phone.svg"
                        class="img-fluid btn-icon text-dark"
                        :alt="service.phoneNumber"
                      />
                      Call</a
                    >

                    <button
                      @click="removeService(service._id)"
                      class="btn btn-sm btn-light text-danger"
                    >
                      <svg
                        width="20"
                        height="20"
                        viewBox="0 0 24 24"
                        fill="none"
                        stroke="currentColor"
                        stroke-width="2"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                        class="feather feather-trash-2"
                      >
                        <polyline points="3 6 5 6 21 6"></polyline>
                        <path
                          d="M19 6v14a2 2 0 0 1-2 2H7a2 2 0 0 1-2-2V6m3 0V4a2 2 0 0 1 2-2h4a2 2 0 0 1 2 2v2"
                        ></path>
                        <line x1="10" y1="11" x2="10" y2="17"></line>
                        <line x1="14" y1="11" x2="14" y2="17"></line>
                      </svg>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </keep-alive>
      </div>
    </div>
  </div>
</template>

<script>
import httpCommon from "../common/http-common";
var LZUTF8 = require("lzutf8");
import http from "../common/http-common";

export default {
  data() {
    return {
      loading: false,
      vehicle: null,
      services: null,
      billPreview: null,
    };
  },
  methods: {
    newService() {
      const vehicleNo = this.$route.params.VehicleId.toUpperCase();
      // Redirecting to add service page
      if (this.vehicle) {
        this.$router.push({
          path: "/services/new",
          query: {
            vehicleNo,
            vehicleModel: this.vehicle.vehicleModel,
          },
        });
      } else {
        this.$router.push({
          path: "/services/new",
          query: {
            vehicleNo,
          },
        });
      }
    },
    async removeService(_id) {
      const confirmed = confirm("Confirm ?");
      if (confirmed) {
        this.loading = true;
        try {
          const response = await http.delete("/services/remove/" + _id);
          if (response.data.success) {
            this.loading = false;
            alert("Service Removed Successfully!");
          }
        } catch (error) {
          alert(error.response.data.msg);
          this.loading = false;
        }
      }
    },
  },
  async created() {
    /**
     * Fetch services for the vehicle No : params.vehicleNo
     */
    this.loading = true;
    const vehicleNo = this.$route.params.VehicleId;
    const result = await http.get("/services/" + vehicleNo.toString());
    if (result.data.success && result.data.services.length > 0) {
      this.vehicle = result.data.vehicle;
      this.services = result.data.services;
      this.loading = false;
    } else {
      this.loading = false;
    }
  },
};
</script>

